<template>
  <div class="py-4">
    <div class="row">
      <h4 class="text-center mb-4">
        Редактировать Удостоверение №{{ certificate.numCertificate }}
      </h4>

      <div class="certificate">
        <form
          class="certificate-edit-form"
          @submit="onUpdateCertificate"
          @invalid-submit="onInvalidSubmit"
        >
          <div class="my-2 radio-toggle-primary">
            <div
              class="btn-group mb-3"
              role="group"
              aria-label="Basic radio toggle button group"
            >
              <input
                type="radio"
                class="btn-check"
                name="btnradio"
                id="btnradio1"
                value="sheetPipe"
                autocomplete="off"
                v-model="certType"
                @change="changeCertType"
              />
              <label class="btn btn-outline-primary " for="btnradio1"
                >Лист/труба</label
              >
              <input
                type="radio"
                class="btn-check"
                name="btnradio"
                id="btnradio2"
                value="fittings"
                autocomplete="off"
                v-model="certType"
                @change="changeCertType"
              />
              <label class="btn btn-outline-primary" for="btnradio2"
                >Арматура</label
              >
            </div>
          </div>
          <div class="certificate-header text-center px-4 mb-4">
            <div class="certificate-header__title text-uppercase">
              Протокол
            </div>
            <p>Заседания аттестационной комиссии</p>

            <div class="text-start mb-4">
              Номер удостоверения: {{ certificate.numCertificate }}
              <input
                type="text"
                id="certNum"
                placeholder="Номер удостоверения"
                :class="[
                  'form-control',
                  'w-50',
                  'w-md-25',
                  { 'is-invalid': numCertError },
                ]"
                v-model="numCertificate"
              />
              <div class="form-text text-danger" v-if="numCertError">
                {{ numCertError }}
              </div>
            </div>

            <div
              class="certificate-header__info d-flex justify-content-between"
            >
              <div class="certificate-header__info-date">
                от
                {{ protocolDateFormat(certificate.date) }}
                <div class="mb-1">
                  <input
                    type="date"
                    id="protocolDate"
                    placeholder="Дата протокола"
                    :class="['form-control', { 'is-invalid': dError }]"
                    v-model="date"
                  />
                  <div class="form-text text-danger" v-if="dError">
                    {{ dError }}
                  </div>
                </div>
              </div>
              <div class="certificate-header__info-num">
                {{ certificate.numProtocol }}
                <div class="mb-1">
                  <input
                    type="text"
                    id="protocolNum"
                    placeholder="Номер протокола"
                    :class="['form-control', { 'is-invalid': numProtError }]"
                    v-model="numProtocol"
                  />
                  <div class="form-text text-danger" v-if="numProtError">
                    {{ numProtError }}
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="certificate-body">
            <p class="text-decoration-underline">
              {{ certificate.textHead }}
            </p>
            <div class="mb-3">
              <div class="form-floating ">
                <textarea
                  placeholder="Текст шапки"
                  id="textHead"
                  :class="[
                    'form-control',
                    'certificate-text-head',
                    { 'is-invalid': tHeadError },
                  ]"
                  v-model="textHead"
                ></textarea>
                <label for="textHead">Текст шапки</label>
              </div>
              <div class="form-text text-danger" v-if="tHeadError">
                {{ tHeadError }}
              </div>
            </div>

            <div class="table-responsive">
              <table class="table-main mb-4 w-100">
                <tr class="d-flex">
                  <th scope="row">1.</th>
                  <td class="w-50">Фамилия</td>
                  <td class="w-50 border-bottom">
                    {{ certificate.firstname }}
                    <div class="mb-1">
                      <input
                        type="text"
                        class="form-control mb-1"
                        placeholder="Фамилия"
                        v-model="firstname"
                      />
                      <div class="form-text text-danger" v-if="fNameError">
                        {{ fNameError }}
                      </div>
                    </div>
                  </td>
                </tr>
                <tr class="d-flex">
                  <th scope="row"></th>
                  <td class="w-50">Имя</td>
                  <td class="w-50 border-bottom">
                    {{ certificate.secondname }}
                    <div class="mb-1">
                      <input
                        type="text"
                        class="form-control mb-1"
                        placeholder="Имя"
                        v-model="secondname"
                      />
                      <div class="form-text text-danger" v-if="sNameError">
                        {{ sNameError }}
                      </div>
                    </div>
                  </td>
                </tr>
                <tr class="d-flex">
                  <th scope="row"></th>
                  <td class="w-50">Отчество</td>
                  <td class="w-50 border-bottom">
                    {{ certificate.lastname }}
                    <div class="mb-1">
                      <input
                        type="text"
                        class="form-control mb-1"
                        placeholder="Отчество"
                        v-model="lastname"
                      />
                      <div class="form-text text-danger" v-if="lNameError">
                        {{ lNameError }}
                      </div>
                    </div>
                  </td>
                </tr>
                <tr class="d-flex">
                  <th scope="row">2.</th>
                  <td class="w-50">Дата рождения, паспорт</td>
                  <td class="w-50 border-bottom">
                    {{ dateFormat(certificate.birthday) }} г.,
                    <div class="mb-1">
                      <input
                        type="date"
                        class="form-control mb-1"
                        placeholder="Дата рождения"
                        v-model="birthday"
                      />
                      <div class="form-text text-danger" v-if="bDayError">
                        {{ bDayError }}
                      </div>
                    </div>

                    паспорт
                    {{ certificate.passport }}
                    <div class="mb-1">
                      <input
                        type="text"
                        class="form-control mb-1"
                        placeholder="Паспорт"
                        v-model="passport"
                      />
                      <div class="form-text text-danger" v-if="passportError">
                        {{ passportError }}
                      </div>
                    </div>
                  </td>
                </tr>
                <tr class="d-flex">
                  <th scope="row">3.</th>
                  <td class="w-50">
                    Номер аттестационного удостоверения сварщика, дата выдачи
                    (при наличии)
                  </td>
                  <td class="w-50 border-bottom">
                    {{
                      certificate.numOldCert
                        ? certificate.numOldCert + ", "
                        : ""
                    }}

                    <div class="mb-1">
                      <input
                        type="text"
                        class="form-control mb-1"
                        placeholder="Номер аттестационного удостоверения сварщика (при наличии)"
                        v-model="numOldCert"
                      />
                      <div class="form-text text-danger" v-if="numOldCertError">
                        {{ numOldCertError }}
                      </div>
                    </div>

                    {{
                      certificate.dateOldCert
                        ? dateFormat(certificate.dateOldCert)
                        : ""
                    }}
                    <div class="mb-1">
                      <input
                        type="date"
                        class="form-control mb-1"
                        placeholder="Дата выдачи
                      (при наличии)"
                        v-model="dateOldCert"
                      />
                      <div
                        class="form-text text-danger"
                        v-if="dateOldCertError"
                      >
                        {{ dateOldCertError }}
                      </div>
                    </div>
                  </td>
                </tr>
                <tr class="d-flex">
                  <th scope="row">4.</th>
                  <td class="w-50"><b>Сведения о работе:</b></td>
                  <td class="w-50"></td>
                </tr>
                <tr class="d-flex">
                  <th scope="row"></th>
                  <td class="w-50">Место работы</td>
                  <td class="w-50 border-bottom">
                    {{ certificate.work }}
                    <div class="mb-1">
                      <input
                        type="text"
                        class="form-control mb-1"
                        placeholder="Место работы"
                        v-model="work"
                      />
                      <div class="form-text text-danger" v-if="wError">
                        {{ wError }}
                      </div>
                    </div>
                  </td>
                </tr>
                <tr class="d-flex">
                  <th scope="row"></th>
                  <td class="w-50">Стаж работы по сварке</td>
                  <td class="w-50 border-bottom">
                    {{ certificate.workYears }}
                    <div class="mb-1">
                      <input
                        type="text"
                        class="form-control mb-1"
                        placeholder="Стаж работы по сварке"
                        v-model="workYears"
                      />
                      <div class="form-text text-danger" v-if="wYearsError">
                        {{ wYearsError }}
                      </div>
                    </div>
                  </td>
                </tr>
                <tr class="d-flex">
                  <th scope="row"></th>
                  <td class="w-50">Квалификационный разряд</td>
                  <td class="w-50 border-bottom">
                    {{ certificate.qualifyingRank }}
                    <div class="mb-1">
                      <div class="form-floating">
                        <select
                          aria-label=".form-select-lg"
                          id="qualifyingRank"
                          :class="[
                            'form-select',
                            'form-select-lg',
                            { 'is-invalid': qRankError },
                          ]"
                          v-model="qualifyingRank"
                        >
                          <option selected disabled
                            >Квалификационный разряд</option
                          >
                          <option
                            v-for="option in optionsQualifyingRank"
                            :key="option.id"
                            :value="option.value"
                            >{{ option.value }}</option
                          >
                        </select>
                        <label for="qualifyingRank"
                          >Квалификационный разряд</label
                        >
                      </div>
                      <div class="form-text text-danger" v-if="qRankError">
                        {{ qRankError }}
                      </div>
                    </div>
                  </td>
                </tr>
                <tr class="d-flex">
                  <th scope="row">5.</th>
                  <td class="w-50">Вид аттестации</td>
                  <td class="w-50 border-bottom">
                    {{ certificate.attestationType }}
                    <div class="mb-1">
                      <div class="form-floating">
                        <select
                          id="attestationType"
                          aria-label=".form-select-lg"
                          :class="[
                            'form-select',
                            'form-select-lg',
                            { 'is-invalid': aTypeError },
                          ]"
                          v-model="attestationType"
                        >
                          <option selected disabled>Вид аттестации</option>
                          <option
                            v-for="option in optionsAttestationType"
                            :key="option.id"
                            :value="option.value"
                            >{{ option.value }}</option
                          >
                        </select>
                        <label for="attestationType">Вид аттестации</label>
                      </div>

                      <div class="form-text text-danger" v-if="aTypeError">
                        {{ aTypeError }}
                      </div>
                    </div>
                  </td>
                </tr>

                <div class="my-3">
                  <ul class="nav nav-tabs" id="tabVariable" role="tablist">
                    <li
                      class="nav-item"
                      :class="{
                        'nav-item-danger':
                          sError ||
                          wMethodError ||
                          wTypeError ||
                          wSeamError ||
                          wConnectionError ||
                          wpError ||
                          wjError ||
                          apError ||
                          pError ||
                          hTreatmentError ||
                          bError ||
                          tError ||
                          diameterError ||
                          eError ||
                          fError,
                      }"
                      role="presentation"
                    >
                      <button
                        class="nav-link active"
                        id="varMain-tab"
                        data-bs-toggle="tab"
                        data-bs-target="#varMain"
                        type="button"
                        role="tab"
                        aria-controls="varMain"
                        aria-selected="true"
                      >
                        <span class="nav-link__text">Основные</span>
                        <span class="nav-link__icon">
                          <svg
                            xmlns="http://www.w3.org/2000/svg"
                            width="20"
                            height="20"
                            fill="currentColor"
                            class="bi bi-sliders2"
                            viewBox="0 0 16 16"
                          >
                            <path
                              fill-rule="evenodd"
                              d="M10.5 1a.5.5 0 0 1 .5.5v4a.5.5 0 0 1-1 0V4H1.5a.5.5 0 0 1 0-1H10V1.5a.5.5 0 0 1 .5-.5ZM12 3.5a.5.5 0 0 1 .5-.5h2a.5.5 0 0 1 0 1h-2a.5.5 0 0 1-.5-.5Zm-6.5 2A.5.5 0 0 1 6 6v1.5h8.5a.5.5 0 0 1 0 1H6V10a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5ZM1 8a.5.5 0 0 1 .5-.5h2a.5.5 0 0 1 0 1h-2A.5.5 0 0 1 1 8Zm9.5 2a.5.5 0 0 1 .5.5v4a.5.5 0 0 1-1 0V13H1.5a.5.5 0 0 1 0-1H10v-1.5a.5.5 0 0 1 .5-.5Zm1.5 2.5a.5.5 0 0 1 .5-.5h2a.5.5 0 0 1 0 1h-2a.5.5 0 0 1-.5-.5Z"
                            />
                          </svg>
                        </span>
                      </button>
                    </li>
                    <li class="nav-item" role="presentation">
                      <button
                        class="nav-link"
                        id="varSecondary-tab"
                        data-bs-toggle="tab"
                        data-bs-target="#varSecondary"
                        type="button"
                        role="tab"
                        aria-controls="varSecondary"
                        aria-selected="false"
                      >
                        <span class="nav-link__text">Дополнительно</span>
                        <span class="nav-link__icon">
                          <svg
                            xmlns="http://www.w3.org/2000/svg"
                            width="20"
                            height="20"
                            fill="currentColor"
                            class="bi bi-plus-square"
                            viewBox="0 0 16 16"
                          >
                            <path
                              d="M14 1a1 1 0 0 1 1 1v12a1 1 0 0 1-1 1H2a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1h12zM2 0a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2H2z"
                            />
                            <path
                              d="M8 4a.5.5 0 0 1 .5.5v3h3a.5.5 0 0 1 0 1h-3v3a.5.5 0 0 1-1 0v-3h-3a.5.5 0 0 1 0-1h3v-3A.5.5 0 0 1 8 4z"
                            />
                          </svg>
                        </span>
                      </button>
                    </li>
                    <li class="nav-item" role="presentation">
                      <button
                        class="nav-link"
                        id="varThird-tab"
                        data-bs-toggle="tab"
                        data-bs-target="#varThird"
                        type="button"
                        role="tab"
                        aria-controls="varThird"
                        aria-selected="false"
                      >
                        <span class="nav-link__text">Дополнительно</span>
                        <span class="nav-link__icon">
                          <svg
                            xmlns="http://www.w3.org/2000/svg"
                            width="20"
                            height="20"
                            fill="currentColor"
                            class="bi bi-plus-square"
                            viewBox="0 0 16 16"
                          >
                            <path
                              d="M14 1a1 1 0 0 1 1 1v12a1 1 0 0 1-1 1H2a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1h12zM2 0a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2H2z"
                            />
                            <path
                              d="M8 4a.5.5 0 0 1 .5.5v3h3a.5.5 0 0 1 0 1h-3v3a.5.5 0 0 1-1 0v-3h-3a.5.5 0 0 1 0-1h3v-3A.5.5 0 0 1 8 4z"
                            />
                          </svg>
                        </span>
                      </button>
                    </li>
                  </ul>
                  <div class="tab-content" id="tabVariableContent">
                    <div
                      class="tab-pane px-3 fade show active"
                      id="varMain"
                      role="tabpanel"
                      aria-labelledby="varMain-tab"
                    >
                      <tr class="d-flex">
                        <th scope="row">6.</th>
                        <td class="w-50">
                          <b
                            >Характеристика контрольного сварного соединения:</b
                          >
                        </td>
                        <td class="w-50"></td>
                      </tr>
                      <tr class="d-flex">
                        <th scope="row">6.1.</th>
                        <td class="w-50">Маркировка образца (клеймо)</td>
                        <td class="w-50 border-bottom">
                          {{ certificate.stigma[0] }}
                          <div class="mb-1">
                            <input
                              type="text"
                              :class="[
                                'form-control',
                                'mb-1',
                                { 'is-invalid': sError },
                              ]"
                              placeholder="Маркировка образца (клеймо)"
                              v-model="stigma"
                              @change="onChangeItem(stigmaVal, 0, stigma)"
                            />
                            <div class="form-text text-danger" v-if="sError">
                              {{ sError }}
                            </div>
                          </div>
                        </td>
                      </tr>
                      <tr class="d-flex">
                        <th scope="row"></th>
                        <td class="w-50">
                          Маркировка образца (клеймо)(общее значение)
                        </td>
                        <td class="w-50 border-bottom">
                          {{ certificate.stigmaGeneral }}
                          <div class="mb-1">
                            <input
                              type="text"
                              :class="[
                                'form-control',
                                'mb-1',
                                { 'is-invalid': sGError },
                              ]"
                              placeholder="Маркировка образца (клеймо)(общее значение)"
                              v-model="stigmaGeneral"
                            />
                            <div class="form-text text-danger" v-if="sGError">
                              {{ sGError }}
                            </div>
                          </div>
                        </td>
                      </tr>
                      <tr class="d-flex">
                        <th scope="row">6.2.</th>
                        <td class="w-50">Способ сварки</td>
                        <td class="w-50 border-bottom">
                          {{ certificate.weldingMethod[0] }}
                          <div class="mb-1">
                            <div class="form-floating">
                              <select
                                id="weldingMethod"
                                aria-label=".form-select-lg"
                                :class="[
                                  'form-select',
                                  'form-select-lg',
                                  { 'is-invalid': wMethodError },
                                ]"
                                v-model="weldingMethod"
                                @change="
                                  onChangeItem(
                                    weldingMethodVal,
                                    0,
                                    weldingMethod
                                  )
                                "
                              >
                                <option selected disabled>Способ сварки</option>
                                <option
                                  v-for="option in optionsWeldingMethod"
                                  :key="option.id"
                                  :value="option.value"
                                  >{{ option.value }}</option
                                >
                              </select>
                              <label for="weldingMethod">Способ сварки</label>
                            </div>

                            <div
                              class="form-text text-danger"
                              v-if="wMethodError"
                            >
                              {{ wMethodError }}
                            </div>
                          </div>
                        </td>
                      </tr>
                      <tr class="d-flex">
                        <th scope="row">6.3.</th>
                        <td class="w-50">Вид свариваемых деталей</td>
                        <td class="w-50 border-bottom">
                          {{
                            certificate.weldedType[0]
                              ? certificate.weldedType[0] + ""
                              : undefined
                          }}

                          <div class="mb-1">
                            <app-checkbox-group
                              title="Вид свариваемых деталей:"
                              :options="optionsWeldedType"
                              :defaultOptions="certificate.weldedType[0]"
                              @checked="onCheckedWeldedType"
                            >
                            </app-checkbox-group>

                            <div
                              class="form-text text-danger"
                              v-if="wTypeError"
                            >
                              {{ wTypeError }}
                            </div>
                          </div>
                        </td>
                      </tr>
                      <tr class="d-flex">
                        <th scope="row">6.4.</th>
                        <td class="w-50">
                          Тип шва, вид и характеристика сварного соединения
                        </td>
                        <td class="w-50 border-bottom">
                          {{
                            certificate.weldedSeam[0]
                              ? certificate.weldedSeam[0] + ""
                              : undefined
                          }}
                          <div class="mb-1">
                            <app-checkbox-group
                              title="Тип сварного шва:"
                              :options="optionsWeldedSeam"
                              :defaultOptions="certificate.weldedSeam[0]"
                              @checked="onCheckedWeldedSeam"
                            >
                            </app-checkbox-group>

                            <div
                              class="form-text text-danger"
                              v-if="wSeamError"
                            >
                              {{ wSeamError }}
                            </div>
                          </div>
                          {{
                            certificate.weldedConnection[0]
                              ? certificate.weldedConnection[0] + ""
                              : undefined
                          }}
                          <div class="mb-1">
                            <app-checkbox-group
                              title="Тип и вид соединения:"
                              :options="optionsWeldedConnection"
                              :defaultOptions="certificate.weldedConnection[0]"
                              @checked="onCheckedWeldedConnection"
                            >
                            </app-checkbox-group>
                            <div
                              class="form-text text-danger"
                              v-if="wConnectionError"
                            >
                              {{ wConnectionError }}
                            </div>
                          </div>
                        </td>
                      </tr>
                      <tr class="d-flex">
                        <th scope="row">6.5.</th>
                        <td class="w-50">Положение при сварке</td>
                        <td class="w-50 border-bottom">
                          {{
                            certificate.weldedPosition[0]
                              ? certificate.weldedPosition[0] + ""
                              : undefined
                          }}
                          <div class="mb-1">
                            <app-checkbox-group
                              title="Положение при сварке:"
                              :options="optionsWeldedPosition"
                              :defaultOptions="certificate.weldedPosition[0]"
                              @checked="onCheckedWeldedPosition"
                            >
                            </app-checkbox-group>

                            <div class="form-text text-danger" v-if="wpError">
                              {{ wpError }}
                            </div>
                          </div>
                        </td>
                      </tr>
                      <tr class="d-flex">
                        <th scope="row">6.6.</th>
                        <td v-if="certType == 'sheetPipe'" class="w-50">
                          Предварительный и сопутствующий подогрев
                        </td>
                        <td v-if="certType == 'fittings'" class="w-50">
                          Положение осей стержней
                        </td>
                        <td
                          v-if="certType == 'sheetPipe'"
                          class="w-50 border-bottom"
                        >
                          {{ certificate.preheating[0] }}
                          <div class="mb-1">
                            <input
                              type="text"
                              placeholder="Предварительный и сопутствующий подогрев"
                              :class="[
                                'form-control',
                                'mb-1',
                                { 'is-invalid': pError },
                              ]"
                              v-model="preheating"
                              @change="
                                onChangeItem(preheatingVal, 0, preheating)
                              "
                            />

                            <div class="form-text text-danger" v-if="pError">
                              {{ pError }}
                            </div>
                          </div>
                        </td>
                        <td
                          v-if="certType == 'fittings'"
                          class="w-50 border-bottom"
                        >
                          {{
                            certificate.axesPosition[0]
                              ? certificate.axesPosition[0] + ""
                              : undefined
                          }}
                          <div class="mb-1">
                            <app-checkbox-group
                              title="Положение осей стержней:"
                              :options="optionsAxesPosition"
                              :defaultOptions="axesPositionDefault"
                              @checked="onCheckedAxesPosition"
                            >
                            </app-checkbox-group>

                            <div class="form-text text-danger" v-if="apError">
                              {{ apError }}
                            </div>
                          </div>
                        </td>
                      </tr>
