                            certificate.axesPosition[2] + ""
                          }}</span>
                          <div class="mb-1">
                            <app-checkbox-group
                              title="Положение осей стержней:"
                              :options="optionsAxesPosition"
                              :defaultOptions="axesPositionDefault3"
                              @checked="onCheckedAxesPosition3"
                            >
                            </app-checkbox-group>
                          </div>
                        </td>
                      </tr>
                      <tr class="d-flex">
                        <th scope="row">6.7.</th>
                        <td v-if="certType == 'sheetPipe'" class="w-50">
                          Термическая обработка
                        </td>
                        <td v-if="certType == 'fittings'" class="w-50">
                          Тип сварного соединения
                        </td>
                        <td
                          v-if="certType == 'sheetPipe'"
                          class="w-50 border-bottom"
                        >
                          <span v-if="certificate.heatTreatment[2]">{{
                            certificate.heatTreatment[2]
                          }}</span>
                          <div class="mb-1">
                            <input
                              type="text"
                              placeholder="Термическая обработка"
                              class="form-control"
                              v-model="heatTreatment3"
                              @change="
                                onChangeItem(
                                  heatTreatmentVal,
                                  2,
                                  heatTreatment3
                                )
                              "
                            />
                          </div>
                        </td>
                        <td
                          v-if="certType == 'fittings'"
                          class="w-50 border-bottom"
                        >
                          <span v-if="certificate.weldedJoint[2]">{{
                            certificate.weldedJoint[2] + ""
                          }}</span>
                          <div class="mb-1">
                            <app-checkbox-group
                              title="Тип сварного соединения:"
                              :options="optionsWeldedJoint"
                              :defaultOptions="weldedJointDefault3"
                              @checked="onCheckedWeldedJoint3"
                            >
                            </app-checkbox-group>
                          </div>
                        </td>
                      </tr>
                      <tr class="d-flex">
                        <th scope="row">7.</th>
                        <td class="w-50"><b>Материал образца:</b></td>
                        <td class="w-50"></td>
                      </tr>
                      <tr class="d-flex">
                        <th scope="row">7.1.</th>
                        <td class="w-50">Марка и группа</td>
                        <td class="w-50 border-bottom">
                          <span v-if="certificate.brand[2]">{{
                            certificate.brand[2] + ""
                          }}</span>
                          <div class="mb-1">
                            <app-checkbox-group
                              title="Марка и группа:"
                              :options="optionsBrand"
                              :defaultOptions="certificate.brand[2]"
                              @checked="onCheckedBrand3"
                            >
                            </app-checkbox-group>
                          </div>
                        </td>
                      </tr>
                      <tr class="d-flex">
                        <th scope="row">7.2.</th>
                        <td class="w-50">Толщина образца (мм)</td>
                        <td class="w-50 border-bottom">
                          <span v-if="certificate.thickness[2]">{{
                            certificate.thickness[2]
                          }}</span>
                          <div class="mb-1">
                            <input
                              type="text"
                              placeholder="Толщина образца (мм)"
                              class="form-control"
                              v-model="thickness3"
                              @change="
                                onChangeItem(thicknessVal, 2, thickness3)
                              "
                            />
                          </div>
                        </td>
                      </tr>
                      <tr class="d-flex">
                        <th scope="row">7.3.</th>
                        <td v-if="certType == 'sheetPipe'" class="w-50">
                          Наружный диаметр трубы (мм)
                        </td>
                        <td v-if="certType == 'fittings'" class="w-50">
                          Диаметр стержня (мм)
                        </td>
                        <td class="w-50 border-bottom">
                          <span v-if="certificate.diameter[2]">{{
                            certificate.diameter[2]
                          }}</span>
                          <div class="mb-1">
                            <input
                              type="text"
                              :placeholder="
                                certType == 'sheetPipe'
                                  ? 'Наружный диаметр трубы (мм)'
                                  : 'Диаметр стержня (мм)'
                              "
                              class="form-control"
                              v-model="diameter3"
                              @change="onChangeItem(diameterVal, 2, diameter3)"
                            />
                          </div>
                        </td>
                      </tr>
                      <tr class="d-flex">
                        <th scope="row">8.</th>
                        <td class="w-50"><b>Сварочные материалы:</b></td>
                        <td class="w-50"></td>
                      </tr>
                      <tr class="d-flex">
                        <th scope="row">8.1.</th>
                        <td class="w-50">Электрод или присадочная проволока</td>
                        <td class="w-50 border-bottom">
                          <span v-if="certificate.electrode[2]">{{
                            certificate.electrode[2] + ""
                          }}</span>
                          <div class="mb-1">
                            <app-checkbox-group
                              title="Электрод или присадочная проволока:"
                              :options="optionsElectrode"
                              :defaultOptions="certificate.electrode[2]"
                              @checked="onCheckedElectrode3"
                            >
                            </app-checkbox-group>
                          </div>
                        </td>
                      </tr>
                      <tr class="d-flex">
                        <th scope="row">8.2.</th>
                        <td class="w-50">Защитный газ и флюс</td>
                        <td class="w-50 border-bottom">
                          <span v-if="certificate.flux[2]">{{
                            certificate.flux[2]
                          }}</span>
                          <div class="mb-1">
                            <input
                              type="text"
                              placeholder="Защитный газ и флюс"
                              class="form-control"
                              v-model="flux3"
                              @change="onChangeItem(fluxVal, 2, flux3)"
                            />
                          </div>
                        </td>
                      </tr>
                    </div>
                  </div>
                </div>

                <tr class="d-flex">
                  <th scope="row">9.</th>
                  <td class="w-50">
                    <b>Результаты контроля качества образца:</b>
                  </td>
                  <td class="w-50"></td>
                </tr>

                <tr class="d-flex" v-if="certificate.controls[0]">
                  <th scope="row">9.1.</th>
                  <td class="w-50">
                    {{
                      certificate.controls[0]
                        ? certificate.controls[0].type
                        : ""
                    }}
                  </td>
                  <td class="w-50 ">
                    <div class="row px-2">
                      <div
                        v-for="(control, idx) in certificate.controls[0].values"
                        :key="idx"
                        :class="[
                          { 'col-4': certificate.controls.length == 3 },
                          {
                            'col-6': certificate.controls.length == 2,
                          },
                          {
                            'col-12': certificate.controls.length == 1,
                          },
                          {
                            'border-start': idx > 0,
                          },
                          'text-center',
                          'overflow-auto',
                          'px-0',
                        ]"
                      >
                        <div class="border-bottom min-h-25">
                          {{ control ? control.grade : "" }}
                        </div>
                        <div class="border-bottom min-h-25">
                          {{
                            control
                              ? "Акт №" +
                                control.num +
                                " от " +
                                dateFormat(control.date)
                              : ""
                          }}
                        </div>
                      </div>
                    </div>
                  </td>
                </tr>
                <tr
                  v-if="
                    certificate.controls.length > 1 && certificate.controls[1]
                  "
                  class="d-flex"
                >
                  <th scope="row">9.2.</th>
                  <td class="w-50">
                    {{ certificate.controls[1].type }}
                  </td>
                  <td class="w-50 ">
                    <div class="row px-2">
                      <div
                        v-for="(control, idx) in certificate.controls[1].values"
                        :key="idx"
                        :class="[
                          { 'col-4': certificate.controls.length == 3 },
                          {
                            'col-6': certificate.controls.length == 2,
                          },
                          {
                            'col-12': certificate.controls.length == 1,
                          },
                          {
                            'border-start': idx > 0,
                          },
                          'text-center',
                          'overflow-auto',
                          'px-0',
                        ]"
                      >
                        <div class="border-bottom min-h-25">
                          {{ control ? control.grade : "" }}
                        </div>
                        <div class="border-bottom min-h-25">
                          {{
                            control
                              ? "Акт №" +
                                control.num +
                                " от " +
                                dateFormat(control.date)
                              : ""
                          }}
                        </div>
                      </div>
                    </div>
                  </td>
                </tr>
                <tr
                  v-if="
                    certificate.controls.length > 2 && certificate.controls[2]
                  "
                  class="d-flex"
                >
                  <th scope="row">9.3.</th>
                  <td class="w-50">
                    {{ certificate.controls[2].type }}
                  </td>
                  <td class="w-50 ">
                    <div class="row px-2">
                      <div
                        v-for="(control, idx) in certificate.controls[2].values"
                        :key="idx"
                        :class="[
                          { 'col-4': certificate.controls.length == 3 },
                          {
                            'col-6': certificate.controls.length == 2,
                          },
                          {
                            'col-12': certificate.controls.length == 1,
                          },
                          {
                            'border-start': idx > 0,
                          },
                          'text-center',
                          'overflow-auto',
                          'px-0',
                        ]"
                      >
                        <div class="border-bottom min-h-25">
                          {{ control ? control.grade : "" }}
                        </div>
                        <div class="border-bottom min-h-25">
                          {{
                            control
                              ? "Акт №" +
                                control.num +
                                " от " +
                                dateFormat(control.date)
                              : ""
                          }}
                        </div>
                      </div>
                    </div>
                  </td>
                </tr>

                <tr class="mb-3 px-3">
                  <certificate-form-quality-control
                    :optionsGrade="optionsGrade"
                    :optionsType="optionsControl"
                    :defaultOptions="certificate.controls"
                    @changeControlsItem="onCheckedQualityControls"
                  ></certificate-form-quality-control>
                  <div class="form-text text-danger mb-3" v-if="controlsError">
                    {{ controlsError }}
                  </div>
                </tr>

                <tr class="d-flex">
                  <th scope="row">10</th>
                  <td class="w-50">
                    Наименование НПА по нормам оценки каче-ства
                  </td>
                  <td class="w-50 border-bottom">
                    {{ certificate.npa + "" }}
                    <div class="mb-1">
                      <input
                        type="text"
                        placeholder="Наименование НПА"
                        :class="[
                          'form-control',
                          'mb-1',
                          { 'is-invalid': npaError },
                        ]"
                        v-model="npa"
                      />

                      <div class="form-text text-danger" v-if="npaError">
                        {{ npaError }}
                      </div>
                    </div>
                  </td>
                </tr>
                <tr class="d-flex">
                  <th scope="row">11</th>
                  <td class="w-50">Оценка теоретических знаний</td>
                  <td class="w-50 border-bottom">
                    {{ certificate.grade }}
                    <div class="mb-1">
                      <div class="form-floating">
                        <select
                          id="grade"
                          aria-label=".form-select-lg"
                          :class="[
                            'form-select',
                            'form-select-lg',
                            { 'is-invalid': gError },
                          ]"
                          v-model="grade"
                        >
                          <option selected disabled
                            >Оценка теоретических знаний</option
                          >
                          <option
                            v-for="option in optionsGrade"
                            :key="option.id"
                            :value="option.value"
                            >{{ option.value }}</option
                          >
                        </select>
                        <label for="grade">Оценка теоретических знаний</label>
                      </div>

                      <div class="form-text text-danger" v-if="gError">
                        {{ gError }}
                      </div>
                    </div>
                  </td>
                </tr>
                <tr class="d-flex">
                  <th scope="row">12</th>
                  <td class="w-50">
                    <b>Решение аттестационной комиссии:</b>
                  </td>
                  <td class="w-50 border-bottom">
                    {{ certificate.decision + " " }}
                    <div class="mb-3">
                      <input
                        type="text"
                        placeholder="Решение аттестационной комиссии"
                        :class="[
                          'form-control',
                          { 'is-invalid': decisionError },
                        ]"
                        v-model="decision"
                      />
                      <div class="form-text text-danger" v-if="decisionError">
                        {{ decisionError }}
                      </div>
                    </div>
                  </td>
                </tr>
                <tr class="d-flex">
                  <th scope="row"></th>
                  <td class="w-100">
                    <div class="text-block">
                      <p>
                        Допущен к ручной дуговой сварке по следующим группам
                        технических устройств опасных производственных объектов:
                      </p>
                      <div class="mb-3">
                        <app-checkbox-dropdown
                          v-for="(item, idx) in checkboxDropdownAccess"
                          :key="item._id"
                          :mainItem="[item.value, item._id]"
                          :items="checkboxDropdownAccessItems"
                          :defaultItems="certificate.accesses"
                          :collapseId="'collapseCheckbox' + idx"
                          @checked="onCheckedAccessSubItems"
                        ></app-checkbox-dropdown>

                        <div class="form-text text-danger" v-if="accessesError">
                          {{ accessesError }}
                        </div>
                      </div>
                    </div>
                  </td>
                </tr>
                <tr class="d-flex">
                  <th scope="row">13.</th>
                  <td class="w-50">Срок периодической аттестации</td>
                  <td class="w-50 border-bottom text-center">
                    {{ dateFormat(certificate.expiration) }} г.
                    <input
                      type="date"
                      class="form-control"
                      id="expiration"
                      placeholder="Срок периодической аттестации"
                      :class="['form-select', { 'is-invalid': expError }]"
                      v-model="expiration"
                    />
                    <div class="form-text text-danger" v-if="expError">
                      {{ expError }}
                    </div>
                  </td>
                </tr>

                <tr class="d-flex">
                  <td class="w-100">
                    Аттестационная комиссия:
                    {{ certificate.comission + "" }}

                    <app-checkbox-group
                      title="Аттестационная комиссия:"
                      :options="optionsComission"
                      :defaultOptions="certificate.comission"
                      @checked="onCheckedComission"
                    >
                    </app-checkbox-group>

                    <div class="form-text text-danger" v-if="сError">
                      {{ сError }}
                    </div>
                  </td>
                </tr>
              </table>

              <h6 class="text-center my-3 fw-bold">
                Сведения о сварке контрольных образцов и область распространения
                аттестации
              </h6>

              <table class="table-main w-100 mb-1">
                <thead>
                  <tr>
                    <td class="border w-50 px-2">
                      Параметры сварки
                    </td>
                    <td class="border w-50 px-2 text-center">
                      Область распространения аттестации
                    </td>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td class="border w-50 px-2">Способ сварки</td>
                    <td class="border w-50 px-2 text-center">
                      {{ certificate.weldingMethodScope + "" }}

                      <div class="mb-1">
                        <div class="form-floating">
                          <select
                            id="weldingMethodScope"
                            aria-label=".form-select-lg"
                            :class="[
                              'form-select',
                              'form-select-lg',
                              { 'is-invalid': wMethodSError },
                            ]"
                            v-model="weldingMethodScope"
                          >
                            <option selected disabled>Способ сварки</option>
                            <option
                              v-for="option in optionsWeldingMethod"
                              :key="option.id"
                              :value="option.value"
                              >{{ option.value }}</option
                            >
                          </select>
                          <label for="weldingMethodScope">Способ сварки</label>
                        </div>

                        <div
                          class="form-text text-danger text-start"
                          v-if="wMethodSError"
                        >
                          {{ wMethodSError }}
                        </div>
                      </div>
                    </td>
                  </tr>
                  <tr>
                    <td class="border w-50 px-2">
                      Вид свариваемых деталей
                    </td>
                    <td class="border w-50 px-2 text-center">
                      {{ certificate.weldedTypeScope + "" }}
                      <div class="mb-1">
                        <app-checkbox-group
                          title="Вид свариваемых деталей:"
                          :options="optionsWeldedType"
                          @checked="onCheckedWeldedTypeScope"
                          :defaultOptions="certificate.weldedTypeScope"
                        >
                        </app-checkbox-group>

                        <div
                          class="form-text text-danger text-start"
                          v-if="wTypeSError"
                        >
                          {{ wTypeSError }}
                        </div>
                      </div>
                    </td>
                  </tr>
                  <tr>
                    <td class="border w-50 px-2">Вид шва</td>
                    <td class="border w-50 px-2 text-center">
                      {{ certificate.weldedSeamScope + "" }}
                      <div class="mb-1">
                        <app-checkbox-group
                          title="Вид шва:"
                          :options="optionsWeldedSeam"
                          @checked="onCheckedWeldedSeamScope"
                          :defaultOptions="certificate.weldedSeamScope"
