                          {{ wSeamSError }}
                        </div>
                      </div>
                    </td>
                  </tr>
                  <tr>
                    <td class="border w-50 px-2">
                      Группа материалов
                    </td>
                    <td class="border w-50 px-2 text-center">
                      {{ certificate.brandScope + "" }}
                      <div class="mb-1">
                        <app-checkbox-group
                          title="Марка и группа:"
                          :options="optionsBrand"
                          @checked="onCheckedBrandScope"
                          :defaultOptions="certificate.brandScope"
                        >
                        </app-checkbox-group>

                        <div
                          class="form-text text-danger text-start"
                          v-if="bSError"
                        >
                          {{ bSError }}
                        </div>
                      </div>
                    </td>
                  </tr>
                  <tr>
                    <td class="border w-50 px-2">
                      Тип присадочного металла
                    </td>
                    <td class="border w-50 px-2 text-center">
                      {{ certificate.electrodeScope + "" }}

                      <div class="mb-1">
                        <app-checkbox-group
                          title="Тип присадочного металла:"
                          :options="optionsElectrode"
                          @checked="onCheckedElectrodeScope"
                          :defaultOptions="certificate.electrodeScope"
                        >
                        </app-checkbox-group>
                        <div
                          class="form-text text-danger text-start"
                          v-if="eSError"
                        >
                          {{ eSError }}
                        </div>
                      </div>
                    </td>
                  </tr>
                  <tr>
                    <td class="border w-50 px-2">
                      Защитный газ (флюс)
                    </td>
                    <td class="border w-50 px-2 text-center">
                      {{ certificate.fluxScope }}

                      <div class="mb-1">
                        <input
                          type="text"
                          id="fluxScope"
                          placeholder="Защитный газ (флюс)"
                          :class="['form-control', { 'is-invalid': fSError }]"
                          v-model="fluxScope"
                        />
                        <div
                          class="form-text text-danger text-start"
                          v-if="fSError"
                        >
                          {{ fSError }}
                        </div>
                      </div>
                    </td>
                  </tr>
                  <tr>
                    <td class="border w-50 px-2">
                      Вспомогательные материалы
                    </td>
                    <td class="border w-50 px-2 text-center">
                      {{ certificate.auxiliaryScope }}

                      <div class="mb-1">
                        <input
                          type="text"
                          placeholder="Вспомогательные материалы"
                          :class="[
                            'form-control',
                            { 'is-invalid': auxiliarySError },
                          ]"
                          v-model="auxiliaryScope"
                        />
                        <div
                          class="form-text text-danger text-start"
                          v-if="auxiliarySError"
                        >
                          {{ auxiliarySError }}
                        </div>
                      </div>
                    </td>
                  </tr>
                  <tr>
                    <td class="border w-50 px-2">
                      Толщина образца, мм
                    </td>
                    <td class="border w-50 px-2 text-center">
                      {{ certificate.thicknessScope }}
                      <div class="mb-1">
                        <input
                          type="text"
                          placeholder="Толщина образца (мм)"
                          :class="['form-control', { 'is-invalid': tSError }]"
                          v-model="thicknessScope"
                        />
                        <div
                          class="form-text text-danger text-start"
                          v-if="tSError"
                        >
                          {{ tSError }}
                        </div>
                      </div>
                    </td>
                  </tr>
                  <tr>
                    <td v-if="certType == 'sheetPipe'" class="border w-50 px-2">
