                            certType == 'sheetPipe'
                              ? 'Наружный диаметр трубы (мм)'
                              : 'Диаметр стержня (мм)'
                          "
                          :class="[
                            'form-control',
                            { 'is-invalid': diameterSError },
                          ]"
                          v-model="diameterScope"
                        />
                        <div
                          class="form-text text-danger text-start"
                          v-if="diameterSError"
                        >
                          {{ diameterSError }}
                        </div>
                      </div>
                    </td>
                  </tr>
                  <tr>
                    <td class="border w-50 px-2">
                      Положение при сварке
                    </td>
                    <td class="border w-50 px-2 text-center">
                      {{ certificate.weldedPositionScope + "" }}

                      <div class="mb-1">
                        <app-checkbox-group
                          title="Положение при сварке:"
                          :options="optionsWeldedPosition"
                          @checked="onCheckedWeldedPositionScope"
                          :defaultOptions="certificate.weldedPositionScope"
                        >
                        </app-checkbox-group>
                        <div
                          class="form-text text-danger text-start"
                          v-if="wpSError"
                        >
                          {{ wpSError }}
                        </div>
                      </div>
                    </td>
                  </tr>
                  <tr>
                    <td class="border w-50 px-2">
                      Исполнение сварного шва
                    </td>
                    <td class="border w-50 px-2 text-center">
                      {{ certificate.weldedConnectionScope + "" }}

                      <div class="mb-1">
                        <app-checkbox-group
                          title="Исполнение сварного шва:"
                          :options="optionsWeldedConnection"
                          @checked="onCheckedWeldedConnectionScope"
                          :defaultOptions="certificate.weldedConnectionScope"
                        >
                        </app-checkbox-group>
                        <div
                          class="form-text text-danger text-start"
                          v-if="wConnectionSError"
                        >
                          {{ wConnectionSError }}
                        </div>
                      </div>
                    </td>
                  </tr>
                  <tr v-if="certType == 'fittings'">
                    <td class="border w-50 px-2">
                      Положение осей стержней
