                              'col-4': classCol_4,
                            },
                            {
                              'col-6': classCol_6,
                            },
                            {
                              'col-12': classCol_12,
                            },
                            {
                              'border-start': idx > 0,
                            },
                            'text-center',
                            'overflow-auto',
                            'px-0',
                          ]"
                        >
                          <div class="border-bottom min-h-25">
                            {{ control ? control.grade : "" }}
                          </div>
                          <div class="border-bottom min-h-25">
                            {{
                              control
                                ? "Акт №" +
                                  control.num +
                                  " от " +
                                  dateFormat(control.date)
