                            {
                              'col-4': classCol_4,
                            },

                            'border-start',
                            'text-center',
                            'overflow-auto',
                            'px-0',
                          ]"
                        >
                          <div class="border-bottom min-h-25"></div>
                          <div class="border-bottom min-h-25"></div>
                        </div>
                        <div
                          v-if="
                            certificate.controls[0].values.length < 2 &&
                              classCol_6
                          "
                          :class="[
                            {
                              'col-6': classCol_6,
                            },

                            'border-start',
                            'text-center',
                            'overflow-auto',
                            'px-0',
                          ]"
                        >
                          <div class="border-bottom min-h-25"></div>
                          <div class="border-bottom min-h-25"></div>
                        </div>
                      </div>
                    </td>
                  </tr>
                  <tr v-if="certificate.controls.length > 1" class="d-flex">
                    <th scope="row">9.2.</th>
                    <td class="w-50">
                      {{ certificate.controls[1].type }}
                    </td>
                    <td class="w-50 ">
                      <div class="row px-2">
                        <div
                          v-for="(control, idx) in certificate.controls[1]
                            .values"
                          :key="idx"
