                            {
                              'col-4': classCol_4,
                            },
                            {
                              'col-6': classCol_6,
                            },
                            {
                              'col-12': classCol_12,
                            },
                            {
                              'border-start': idx > 0,
                            },
                            'text-center',
                            'overflow-auto',
                            'px-0',
                          ]"
                        >
                          <div class="border-bottom min-h-25">
                            {{ control ? control.grade : "" }}
                          </div>
                          <div class="border-bottom min-h-25">
                            {{
                              control
                                ? "Акт №" +
                                  control.num +
                                  " от " +
                                  dateFormat(control.date)
                                : ""
                            }}
                          </div>
                        </div>
                        <div
                          v-if="
                            certificate.controls[1].values.length < 3 &&
                              classCol_4
                          "
                          :class="[
                            {
                              'col-4': classCol_4,
                            },

                            'border-start',
                            'text-center',
                            'overflow-auto',
                            'px-0',
                          ]"
                        >
                          <div class="border-bottom min-h-25"></div>
                          <div class="border-bottom min-h-25"></div>
                        </div>
                        <div
                          v-if="
                            certificate.controls[1].values.length < 2 &&
                              classCol_6
                          "
                          :class="[
                            {
                              'col-6': classCol_6,
                            },

                            'border-start',
                            'text-center',
                            'overflow-auto',
                            'px-0',
                          ]"
                        >
                          <div class="border-bottom min-h-25"></div>
                          <div class="border-bottom min-h-25"></div>
                        </div>
                      </div>
                    </td>
                  </tr>
                  <tr v-if="certificate.controls.length > 2" class="d-flex">
                    <th scope="row">9.3.</th>
                    <td class="w-50">
                      {{ certificate.controls[2].type }}
                    </td>
                    <td class="w-50 ">
                      <div class="row px-2">
                        <div
                          v-for="(control, idx) in certificate.controls[2]
                            .values"
                          :key="idx"
                          :class="[
                            {
                              'col-4': classCol_4,
                            },
                            {
                              'col-6': classCol_6,
                            },
                            {
                              'col-12': classCol_12,
                            },
                            {
                              'border-start': idx > 0,
                            },
                            'text-center',
                            'overflow-auto',
                            'px-0',
                          ]"
                        >
                          <div class="border-bottom min-h-25">
                            {{ control ? control.grade : "" }}
                          </div>
                          <div class="border-bottom min-h-25">
                            {{
                              control
                                ? "Акт №" +
                                  control.num +
                                  " от " +
                                  dateFormat(control.date)
                                : ""
                            }}
                          </div>
                        </div>
                        <div
                          v-if="
                            certificate.controls[2].values.length < 3 &&
                              classCol_4
                          "
                          :class="[
                            {
                              'col-4': classCol_4,
                            },

                            'border-start',
                            'text-center',
                            'overflow-auto',
                            'px-0',
                          ]"
