                          <div class="border-bottom min-h-25"></div>
                          <div class="border-bottom min-h-25"></div>
                        </div>
                        <div
                          v-if="
                            certificate.controls[2].values.length < 2 &&
                              classCol_6
                          "
                          :class="[
                            {
                              'col-6': classCol_6,
                            },

                            'border-start',
                            'text-center',
                            'overflow-auto',
                            'px-0',
                          ]"
                        >
                          <div class="border-bottom min-h-25"></div>
                          <div class="border-bottom min-h-25"></div>
                        </div>
                      </div>
                    </td>
                  </tr>

                  <tr class="d-flex">
                    <th scope="row">10.</th>
                    <td class="w-50">
                      Наименование НПА по нормам оценки каче-ства
                    </td>
                    <td class="w-50 border-bottom text-center">
