                                {{
                                  arrayFormat(
                                    shortingTableItems(certificate.accesses)
                                  ) + ""
                                }}
                              </td>
                            </tr>
                            <tr>
                              <td class="border w-50 px-2">Способ сварки</td>
                              <td class="border w-50 px-2 text-center">
                                {{ certificate.weldingMethodScope }}
                              </td>
                            </tr>
                            <tr>
                              <td class="border w-50 px-2">
                                Вид свариваемых деталей
                              </td>
                              <td class="border w-50 px-2 text-center">
                                {{
                                  arrayFormat(certificate.weldedTypeScope) + ""
                                }}
                              </td>
                            </tr>
                            <tr>
                              <td class="border w-50 px-2">Вид шва</td>
                              <td class="border w-50 px-2 text-center">
                                {{
                                  arrayFormat(certificate.weldedSeamScope) + ""
                                }}
                              </td>
                            </tr>
                            <tr>
                              <td class="border w-50 px-2">
                                Группа материалов
                              </td>
                              <td class="border w-50 px-2 text-center">
                                {{ arrayFormat(certificate.brandScope) + "" }}
                              </td>
                            </tr>
                            <tr>
                              <td class="border w-50 px-2">
                                Тип присадочного металла
                              </td>
                              <td class="border w-50 px-2 text-center">
                                {{
                                  arrayFormat(certificate.electrodeScope) + ""
                                }}
                              </td>
                            </tr>
                            <tr>
                              <td class="border w-50 px-2">
                                Защитный газ (флюс)
                              </td>
                              <td class="border w-50 px-2 text-center">
                                {{ certificate.fluxScope }}
                              </td>
                            </tr>
                            <tr>
                              <td class="border w-50 px-2">
                                Вспомогательные материалы
                              </td>
                              <td class="border w-50 px-2 text-center">
                                {{ certificate.auxiliaryScope }}
                              </td>
                            </tr>
                            <tr>
                              <td class="border w-50 px-2">
                                Толщина образца, мм
                              </td>
                              <td class="border w-50 px-2 text-center">
                                {{ certificate.thicknessScope }}
                              </td>
                            </tr>
                            <tr>
                              <td
                                v-if="certificate.certType == 'sheetPipe'"
                                class="border w-50 px-2"
                              >
                                Наружный диаметр трубы, мм
                              </td>
                              <td
                                v-if="certificate.certType == 'fittings'"
                                class="border w-50 px-2"
                              >
                                Диаметр стержня, мм
                              </td>
                              <td class="border w-50 px-2 text-center">
                                {{ certificate.diameterScope }}
                              </td>
                            </tr>
                            <tr>
                              <td class="border w-50 px-2">
                                Положение при сварке
                              </td>
                              <td class="border w-50 px-2 text-center">
                                {{
                                  arrayFormat(certificate.weldedPositionScope) +
                                    ""
                                }}
                              </td>
                            </tr>
                            <tr>
                              <td class="border w-50 px-2">
                                Исполнение сварного шва
                              </td>
                              <td class="border w-50 px-2 text-center">
                                {{
                                  arrayFormat(
                                    certificate.weldedConnectionScope
                                  ) + ""
                                }}
                              </td>
                            </tr>
                            <tr v-if="certificate.certType == 'fittings'">
                              <td class="border w-50 px-2">
                                Положение осей стержней
                              </td>
                              <td class="border w-50 px-2 text-center">
                                {{
                                  arrayFormat(certificate.axesPositionScope) +
                                    ""
                                }}
                              </td>
                            </tr>
                            <tr v-if="certificate.certType == 'fittings'">
                              <td class="border w-50 px-2">
                                Тип сварного соединения
                              </td>
                              <td class="border w-50 px-2 text-center">
                                {{
                                  arrayFormat(certificate.weldedJointScope) + ""
                                }}
                              </td>
                            </tr>
                          </tbody>
                        </table>
                      </div>
                    </td>
                  </tr>
                  <tr class="d-flex">
                    <th scope="row">13.</th>
                    <td class="w-50">Срок периодической аттестации</td>
                    <td class="w-50 border-bottom text-center">
                      {{ dateFormat(certificate.expiration) }} г.
                    </td>
                  </tr>

                  <tr class="d-flex">
                    <td class="">
                      Аттестационная комиссия:
                      {{ arrayFormat(certificate.comission) + "" }}
                    </td>
                  </tr>
                  <tr class="d-flex">
                    <td class="">
                      Номер удостоверения: {{ certificate.numCertificate }}
                    </td>
                  </tr>
                </table>
              </div>

              <certificate-qr
                :data="[
                  certificate.firstname,
                  certificate.secondname,
                  certificate.lastname,
                  certificate.numCertificate,
                  dateFormat(certificate.date),
                  $route.params.id,
                ]"
              ></certificate-qr>

              <div
                class="certificate-nav text-center font-family-default d-flex justify-content-center"
              >
                <button
                  class="btn btn-cert-control btn-cert-control-warning rounded-circle mx-3"
                  type="button"
                  title="Редактировать"
                  @click="editCertificate"
                >
                  <span>
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="24"
                      height="24"
                      fill="currentColor"
                      class="bi bi-pencil-square"
                      viewBox="0 0 16 16"
                    >
                      <path
                        d="M15.502 1.94a.5.5 0 0 1 0 .706L14.459 3.69l-2-2L13.502.646a.5.5 0 0 1 .707 0l1.293 1.293zm-1.75 2.456-2-2L4.939 9.21a.5.5 0 0 0-.121.196l-.805 2.414a.25.25 0 0 0 .316.316l2.414-.805a.5.5 0 0 0 .196-.12l6.813-6.814z"
                      />
                      <path
                        fill-rule="evenodd"
                        d="M1 13.5A1.5 1.5 0 0 0 2.5 15h11a1.5 1.5 0 0 0 1.5-1.5v-6a.5.5 0 0 0-1 0v6a.5.5 0 0 1-.5.5h-11a.5.5 0 0 1-.5-.5v-11a.5.5 0 0 1 .5-.5H9a.5.5 0 0 0 0-1H2.5A1.5 1.5 0 0 0 1 2.5v11z"
                      />
                    </svg>
                  </span>
                </button>
                <button
                  class="btn btn-cert-control btn-cert-control-success rounded-circle mx-3"
                  type="button"
                  title="Добавить вкладыш"
                  @click="addSubCertificate"
                >
                  <span>
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="24"
                      height="24"
                      fill="currentColor"
                      class="bi bi-files"
                      viewBox="0 0 16 16"
                    >
                      <path
                        d="M13 0H6a2 2 0 0 0-2 2 2 2 0 0 0-2 2v10a2 2 0 0 0 2 2h7a2 2 0 0 0 2-2 2 2 0 0 0 2-2V2a2 2 0 0 0-2-2zm0 13V4a2 2 0 0 0-2-2H5a1 1 0 0 1 1-1h7a1 1 0 0 1 1 1v10a1 1 0 0 1-1 1zM3 4a1 1 0 0 1 1-1h7a1 1 0 0 1 1 1v10a1 1 0 0 1-1 1H4a1 1 0 0 1-1-1V4z"
                      />
                    </svg>
                  </span>
                </button>
                <button
                  class="btn btn-cert-control btn-cert-control-danger rounded-circle mx-3"
                  type="button"
                  title="Удалить"
                  @click="showModal"
                >
                  <span>
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="24"
                      height="24"
                      fill="currentColor"
                      class="bi bi-trash3"
                      viewBox="0 0 16 16"
                    >
                      <path
                        d="M6.5 1h3a.5.5 0 0 1 .5.5v1H6v-1a.5.5 0 0 1 .5-.5ZM11 2.5v-1A1.5 1.5 0 0 0 9.5 0h-3A1.5 1.5 0 0 0 5 1.5v1H2.506a.58.58 0 0 0-.01 0H1.5a.5.5 0 0 0 0 1h.538l.853 10.66A2 2 0 0 0 4.885 16h6.23a2 2 0 0 0 1.994-1.84l.853-10.66h.538a.5.5 0 0 0 0-1h-.995a.59.59 0 0 0-.01 0H11Zm1.958 1-.846 10.58a1 1 0 0 1-.997.92h-6.23a1 1 0 0 1-.997-.92L3.042 3.5h9.916Zm-7.487 1a.5.5 0 0 1 .528.47l.5 8.5a.5.5 0 0 1-.998.06L5 5.03a.5.5 0 0 1 .47-.53Zm5.058 0a.5.5 0 0 1 .47.53l-.5 8.5a.5.5 0 1 1-.998-.06l.5-8.5a.5.5 0 0 1 .528-.47ZM8 4.5a.5.5 0 0 1 .5.5v8.5a.5.5 0 0 1-1 0V5a.5.5 0 0 1 .5-.5Z"
                      />
                    </svg>
                  </span>
                </button>
                <button
                  class="btn btn-cert-control btn-cert-control-primary rounded-circle mx-3"
                  type="button"
                  title="Печать"
                  @click="print"
                >
                  <span>
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="24"
                      height="24"
                      fill="currentColor"
                      class="bi bi-printer"
                      viewBox="0 0 16 16"
                    >
                      <path d="M2.5 8a.5.5 0 1 0 0-1 .5.5 0 0 0 0 1z" />
                      <path
                        d="M5 1a2 2 0 0 0-2 2v2H2a2 2 0 0 0-2 2v3a2 2 0 0 0 2 2h1v1a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2v-1h1a2 2 0 0 0 2-2V7a2 2 0 0 0-2-2h-1V3a2 2 0 0 0-2-2H5zM4 3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1v2H4V3zm1 5a2 2 0 0 0-2 2v1H2a1 1 0 0 1-1-1V7a1 1 0 0 1 1-1h12a1 1 0 0 1 1 1v3a1 1 0 0 1-1 1h-1v-1a2 2 0 0 0-2-2H5zm7 2v3a1 1 0 0 1-1 1H5a1 1 0 0 1-1-1v-3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1z"
                      />
                    </svg>
                  </span>
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <edit-certificate-form
        v-else-if="showEditFields"
        :values="certificate"
        @hide="showEditFields = false"
        @update="loadingData"
      ></edit-certificate-form>
      <div v-else-if="!isId" class="py-4">
        <h5 class="text-center">
          Удостоверение с ID {{ $route.params.id }} не найдено
        </h5>
        <p class="text-center">
          <router-link to="/">Вернуться на главную</router-link>
        </p>
      </div>
    </div>

    <div v-else class="container">
      <app-loader v-if="loading"></app-loader>

      <div v-else-if="certificate && isId" class="py-4">
        <div class="row">
          <h4 class="text-center mb-0">
            Удостоверение №{{ certificate.numCertificate }}
          </h4>

          <div class="my-4">
            <p class="mb-sm-2 mb-md-0 mb-lg-0 mb-xl-0">
              <span class="check-icon">
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  class="h-6 w-6"
                  fill="none"
                  viewBox="0 0 24 24"
                  stroke="currentColor"
                >
                  <path
                    stroke-linecap="round"
                    stroke-linejoin="round"
                    stroke-width="2"
                    d="M5 13l4 4L19 7"
                  />
                </svg>
              </span>
              Удостоверение действительно до
              {{ dateFormat(certificate.expiration) }}
            </p>
            <p class="mb-sm-2 mb-md-0 mb-lg-0 mb-xl-0">
              В реестр "Университетские технологии" внесено
            </p>
            <p class="mb-sm-2 mb-md-0 mb-lg-0 mb-xl-0">
              {{
                certificate.firstname +
                  " " +
                  certificate.secondname +
                  " " +
                  certificate.lastname
              }}
            </p>
            <p class="mb-sm-2 mb-md-0 mb-lg-0 mb-xl-0">
              {{ certificate.work }}
            </p>
            <p class="mb-sm-2 mb-md-0 mb-lg-0 mb-xl-0">
              {{ shortingTableItems(certificate.accesses) + "" }}
            </p>
          </div>
        </div>
      </div>
      <div v-else-if="!isId" class="py-4">
        <h5 class="text-center">
          Удостоверение с ID {{ $route.params.id }} не найдено
        </h5>
      </div>
    </div>

    <teleport to="body">
      <app-modal
        v-if="modal"
        title="Удалить запись из реестра?"
        @close="closeModal"
      >
        <div class="modal-body">
          Запись удостоверения №{{ certificate.numCertificate }} будет удалена
          из реестра. Вы подтверждаете удаление?
        </div>
        <div class="modal-footer">
          <button
            type="button"
            class="btn btn-success"
            @click="removeCertificate($route.params.id)"
          >
            Да
          </button>
          <button type="button" class="btn btn-danger" @click="closeModal">
            Нет
          </button>
        </div>
      </app-modal>
    </teleport>
  </app-page>
</template>

<script>
import { useRoute, useRouter } from "vue-router";
import { useStore } from "vuex";
import { ref, onMounted, computed } from "vue";
// import { Tooltip } from "bootstrap";
import { dateFormat } from "../utils/dateFormat";
import { protocolDateFormat } from "../utils/protocolDateFormat";
import { shortingTableItems } from "../utils/shortingTableItems";
import { arrayFormat } from "../utils/arrayFormat";
import AppPage from "../components/ui/AppPage.vue";
import AppModal from "../components/AppModal";
import AppLoader from "../components/ui/AppLoader.vue";
import EditCertificateForm from "../components/certificates/EditCertificateForm.vue";
import CertificateQr from "../components/certificates/CertificateQr.vue";

export default {
  setup() {
    const route = useRoute();
    const router = useRouter();
    const store = useStore();
    const loading = ref(true);
    const modal = ref(false);
    const certificate = ref({});
    const auth = store.getters["auth/isAuthenticated"];
    const showEditFields = ref(false);
    const isId = ref(true);

    onMounted(() => {
      loadingData();
    });

    const classCol_4 = computed(() => {
      return (
        certificate.value.stigma.length == 3 ||
        certificate.value.weldingMethod.length == 3 ||
        certificate.value.weldedType.length == 3 ||
        (certificate.value.weldedConnection.length == 3 &&
          certificate.value.weldedSeam.length == 3) ||
        certificate.value.weldedPosition.length == 3 ||
        certificate.value.preheating.length == 3 ||
        certificate.value.axesPosition.length == 3 ||
        certificate.value.heatTreatment.length == 3 ||
        certificate.value.weldedJoint.length == 3 ||
        certificate.value.brand.length == 3 ||
        certificate.value.thickness.length == 3 ||
        certificate.value.diameter.length == 3 ||
        certificate.value.electrode.length == 3 ||
        certificate.value.flux.length == 3 ||
        certificate.value.controls.length == 3
      );
    });

    const classCol_6 = computed(() => {
      return (
        certificate.value.stigma.length < 3 &&
        certificate.value.weldingMethod.length < 3 &&
        certificate.value.weldedType.length < 3 &&
        certificate.value.weldedConnection.length < 3 &&
        certificate.value.weldedSeam.length < 3 &&
        certificate.value.weldedPosition.length < 3 &&
        certificate.value.preheating.length < 3 &&
        certificate.value.axesPosition.length < 3 &&
        certificate.value.heatTreatment.length < 3 &&
        certificate.value.weldedJoint.length < 3 &&
        certificate.value.brand.length < 3 &&
        certificate.value.thickness.length < 3 &&
        certificate.value.diameter.length < 3 &&
        certificate.value.electrode.length < 3 &&
        certificate.value.flux.length < 3 &&
        certificate.value.controls.length < 3 &&
        (certificate.value.stigma.length > 1 ||
          certificate.value.weldingMethod.length > 1 ||
          certificate.value.weldedType.length > 1 ||
          (certificate.value.weldedConnection.length > 1 &&
            certificate.value.weldedSeam.length > 1) ||
          certificate.value.weldedPosition.length > 1 ||
          certificate.value.preheating.length > 1 ||
          certificate.value.axesPosition.length > 1 ||
          certificate.value.heatTreatment.length > 1 ||
          certificate.value.weldedJoint.length > 1 ||
          certificate.value.brand.length > 1 ||
          certificate.value.thickness.length > 1 ||
          certificate.value.diameter.length > 1 ||
          certificate.value.electrode.length > 1 ||
          certificate.value.flux.length > 1 ||
          certificate.value.controls.length > 1)
      );
    });

    const classCol_12 = computed(() => {
      return (
        certificate.value.stigma.length == 1 &&
        certificate.value.weldingMethod.length == 1 &&
        certificate.value.weldedType.length == 1 &&
        certificate.value.weldedConnection.length == 1 &&
        certificate.value.weldedSeam.length == 1 &&
        certificate.value.weldedPosition.length == 1 &&
        certificate.value.preheating.length == 1 &&
        certificate.value.axesPosition.length == 1 &&
        certificate.value.heatTreatment.length == 1 &&
        certificate.value.weldedJoint.length == 1 &&
        certificate.value.brand.length == 1 &&
        certificate.value.thickness.length == 1 &&
        certificate.value.diameter.length == 1 &&
        certificate.value.electrode.length == 1 &&
        certificate.value.flux.length == 1 &&
        certificate.value.controls.length == 1
      );
    });

    const loadingData = async () => {
      try {
        loading.value = true;

        certificate.value = await store.dispatch(
          "certificate/loadById",
          route.params.id
        );

        // console.log("certificate::: ", certificate.value);

        loading.value = false;
      } catch (error) {
        isId.value = false;
        loading.value = false;
      }
    };

    const removeCertificate = async (id) => {
      certificate.value = await store.dispatch("certificate/remove", id);
      modal.value = false;
      router.push("/");
    };

    function showModal() {
      modal.value = true;
      setTimeout(() => {
        let systemModal = document.getElementById("systemModal");
        systemModal.classList.add("show");
      }, 0);
    }

    function closeModal() {
      modal.value = false;
    }

    const editCertificate = async () => {
      loading.value = true;

      try {
        //link для загрузки с сервера
        const linkQualifyingRank = "certificate-qualifying-rank";
        const linkAttestationType = "certificate-attestation-type";
        const linkWeldingMethod = "certificate-welding-method";
        const linkWeldedType = "certificate-welded-type";
        const linkWeldedSeam = "certificate-welded-seam";
        const linkWeldedConnection = "certificate-welded-connection";
        const linkWeldedPosition = "certificate-welded-position";
        const linkWeldedJoint = "certificate-welded-joint";
        const linkAxesPosition = "certificate-axes-position";
        const linkBrand = "certificate-brand";
        const linkElectrode = "certificate-electrode";
        const linkComission = "certificate-comission";
        const linkControl = "certificate-control";
        const linkAccess = "certificate-access";
        const linkAccessItem = "certificate-access-item";
        const linkGrade = "certificate-grade";

        //загрузка значений в store из БД на сервере (выполнение в store экшена load):
        await store.dispatch("certItem/load", linkQualifyingRank);
        await store.dispatch("certItem/load", linkAttestationType);
        await store.dispatch("certItem/load", linkWeldingMethod);
        await store.dispatch("certItem/load", linkWeldedType);
        await store.dispatch("certItem/load", linkWeldedSeam);
        await store.dispatch("certItem/load", linkWeldedConnection);
        await store.dispatch("certItem/load", linkWeldedPosition);
        await store.dispatch("certItem/load", linkWeldedJoint);
        await store.dispatch("certItem/load", linkAxesPosition);
        await store.dispatch("certItem/load", linkBrand);
        await store.dispatch("certItem/load", linkElectrode);
        await store.dispatch("certItem/load", linkComission);
        await store.dispatch("certItem/load", linkControl);
        await store.dispatch("certItem/load", linkAccess);
        await store.dispatch("certItem/load", linkAccessItem);
        await store.dispatch("certItem/load", linkGrade);

        showEditFields.value = true;
        loading.value = false;
      } catch (e) {
        // console.log("e:::", e);
        if (!auth) {
          router.push("/auth?message=auth");
        }
      }
    };

    const addSubCertificate = () => {
      store.dispatch("setMessage", {
        value: "Данный функционал ещё не доступен",
        type: "warning",
      });
    };

    const print = () => {
      window.print();
    };

    return {
      loading,
      isId,
      showModal,
      closeModal,
      certificate,
      dateFormat,
      protocolDateFormat,
      shortingTableItems,
      arrayFormat,
      removeCertificate,
      editCertificate,
      addSubCertificate,
      print,

      auth,
      modal,
      showEditFields,
      loadingData,

      classCol_4,
      classCol_6,
      classCol_12,
    };
  },
  components: {
    AppPage,
    AppLoader,
    AppModal,

    EditCertificateForm,
    CertificateQr,
  },
};
</script>

<style scoped></style>
