<script>
import { ref, onMounted, watch } from "vue";

export default {
  props: {
    optionsType: { type: Array, required: true },
    optionsGrade: { type: Array, required: true },
    defaultOptions: { type: Array },
  },
  emits: ["changeControlsItem", "changeType"],
  setup(props, { emit }) {
    //массив полей
    const controls = ref([]);
    //массива отображения полей
    const controlsShown = [];
    //очередь показа полей
    const controlsOrder1 = ref(1);
    const controlsOrder2 = ref();
    const controlsOrder3 = ref();
    const controlsOrder4 = ref();
    const controlsOrder5 = ref();

    //показывать контроль
    const controls1 = ref(true);
    const controls2 = ref(false);
    const controls3 = ref(false);
    const controls4 = ref(false);
    const controls5 = ref(false);

    const controlsType1 = ref("");
    const controlsType2 = ref("");
    const controlsType3 = ref("");
    const controlsType4 = ref("");
    const controlsType5 = ref("");
    const controlsNum1 = ref("");
    const controlsNum2 = ref("");
    const controlsNum3 = ref("");
    const controlsNum4 = ref("");
    const controlsNum5 = ref("");
    const controlsGrade1 = ref("");
    const controlsGrade2 = ref("");
    const controlsGrade3 = ref("");
    const controlsGrade4 = ref("");
    const controlsGrade5 = ref("");
    const controlsDate1 = ref("");
    const controlsDate2 = ref("");
    const controlsDate3 = ref("");
    const controlsDate4 = ref("");
    const controlsDate5 = ref("");

    onMounted(() => {
      //по умолчанию показывать только первый контроль
      if (controls1.value) {
        controlsShown.push("controls1");
      }

      //когда есть значения по умолчанию
      if (props.defaultOptions) {
        // console.log("props.defaultOptions ::: ", props.defaultOptions);
        // console.log(
        //   "props.defaultOptions.length ::: ",
        //   props.defaultOptions.length
        // );

        switch (props.defaultOptions.length) {
          case 1:
            controlsType1.value = props.defaultOptions[0].control;
            controlsNum1.value = props.defaultOptions[0].num;
            controlsGrade1.value = props.defaultOptions[0].grade;
            controlsDate1.value = props.defaultOptions[0].date;
            break;
          case 2:
            controlsType1.value = props.defaultOptions[0].control;
            controlsNum1.value = props.defaultOptions[0].num;
            controlsGrade1.value = props.defaultOptions[0].grade;
            controlsDate1.value = props.defaultOptions[0].date;
            controlsType2.value = props.defaultOptions[1].control;
            controlsNum2.value = props.defaultOptions[1].num;
            controlsGrade2.value = props.defaultOptions[1].grade;
            controlsDate2.value = props.defaultOptions[1].date;

            controls2.value = true;
            controlsShown.push("controls2");
            controlsOrder2.value = 2;
            break;
          case 3:
            controlsType1.value = props.defaultOptions[0].control;
            controlsNum1.value = props.defaultOptions[0].num;
            controlsGrade1.value = props.defaultOptions[0].grade;
            controlsDate1.value = props.defaultOptions[0].date;
            controlsType2.value = props.defaultOptions[1].control;
            controlsNum2.value = props.defaultOptions[1].num;
            controlsGrade2.value = props.defaultOptions[1].grade;
            controlsDate2.value = props.defaultOptions[1].date;
            controlsType3.value = props.defaultOptions[2].control;
            controlsNum3.value = props.defaultOptions[2].num;
            controlsGrade3.value = props.defaultOptions[2].grade;
            controlsDate3.value = props.defaultOptions[2].date;

            controls2.value = true;
            controls3.value = true;
            controlsShown.push("controls2");
            controlsShown.push("controls3");
            controlsOrder2.value = 2;
            controlsOrder3.value = 3;
            break;
          case 4:
            controlsType1.value = props.defaultOptions[0].control;
            controlsNum1.value = props.defaultOptions[0].num;
            controlsGrade1.value = props.defaultOptions[0].grade;
            controlsDate1.value = props.defaultOptions[0].date;
            controlsType2.value = props.defaultOptions[1].control;
            controlsNum2.value = props.defaultOptions[1].num;
            controlsGrade2.value = props.defaultOptions[1].grade;
            controlsDate2.value = props.defaultOptions[1].date;
            controlsType3.value = props.defaultOptions[2].control;
            controlsNum3.value = props.defaultOptions[2].num;
            controlsGrade3.value = props.defaultOptions[2].grade;
            controlsDate3.value = props.defaultOptions[2].date;
            controlsType4.value = props.defaultOptions[3].control;
            controlsNum4.value = props.defaultOptions[3].num;
            controlsGrade4.value = props.defaultOptions[3].grade;
            controlsDate4.value = props.defaultOptions[3].date;

            controls2.value = true;
            controls3.value = true;
            controls4.value = true;
            controlsShown.push("controls2");
            controlsShown.push("controls3");
            controlsShown.push("controls4");
            controlsOrder2.value = 2;
            controlsOrder3.value = 3;
            controlsOrder4.value = 4;
            break;
          case 5:
            controlsType1.value = props.defaultOptions[0].control;
            controlsNum1.value = props.defaultOptions[0].num;
            controlsGrade1.value = props.defaultOptions[0].grade;
            controlsDate1.value = props.defaultOptions[0].date;
            controlsType2.value = props.defaultOptions[1].control;
            controlsNum2.value = props.defaultOptions[1].num;
            controlsGrade2.value = props.defaultOptions[1].grade;
            controlsDate2.value = props.defaultOptions[1].date;
            controlsType3.value = props.defaultOptions[2].control;
            controlsNum3.value = props.defaultOptions[2].num;
            controlsGrade3.value = props.defaultOptions[2].grade;
            controlsDate3.value = props.defaultOptions[2].date;
            controlsType4.value = props.defaultOptions[3].control;
            controlsNum4.value = props.defaultOptions[3].num;
            controlsGrade4.value = props.defaultOptions[3].grade;
            controlsDate4.value = props.defaultOptions[3].date;
            controlsType5.value = props.defaultOptions[4].control;
            controlsNum5.value = props.defaultOptions[4].num;
            controlsGrade5.value = props.defaultOptions[4].grade;
            controlsDate5.value = props.defaultOptions[4].date;

            controls2.value = true;
            controls3.value = true;
            controls4.value = true;
            controls5.value = true;
            controlsShown.push("controls2");
            controlsShown.push("controls3");
            controlsShown.push("controls4");
            controlsShown.push("controls5");
            controlsOrder2.value = 2;
            controlsOrder3.value = 3;
            controlsOrder4.value = 4;
            controlsOrder5.value = 5;
            break;

          default:
            break;
        }
      }
    });

    function checkControlsFields(val, controlsId) {
      // console.log("controls.value", controls.value);
      // console.log("val: ", val);

      const index = controls.value.findIndex(
        (item) => item.controlId == controlsId
      );
      // console.log("index", index);

      if (val[0] && val[1] && val[2] && val[3]) {
        if (index == -1) {
          controls.value.push({
            controlId: controlsId,
            control: val[0],
            num: val[1].trim(),
            grade: val[2],
            date: val[3],
          });

          //передаем выбранные значения для input
          emit("changeControlsItem", {
            values: controls.value,
          });
          // console.log("controls.value", controls.value);
        } else {
          controls.value.splice(index, 1, {
            controlId: controlsId,
            control: val[0],
            num: val[1].trim(),
            grade: val[2],
            date: val[3],
          });
          //передаем выбранные значения для input
          emit("changeControlsItem", {
            values: controls.value,
          });
          // console.log("controls.value", controls.value);
        }

        // console.log("controls", controls.value);
      } else {
        if (index == -1) {
          return;
        } else {
          controls.value.splice(index, 1);
          //передаем выбранные значения для input
          emit("changeControlsItem", {
            values: controls.value,
          });
          // console.log("controls.value", controls.value);
        }
      }
    }

    watch(
      [controlsType1, controlsNum1, controlsGrade1, controlsDate1],
      (val) => {
        checkControlsFields(val, 1);
      }
    );
    watch(
      [controlsType2, controlsNum2, controlsGrade2, controlsDate2],
      (val) => {
        checkControlsFields(val, 2);
      }
    );
    watch(
      [controlsType3, controlsNum3, controlsGrade3, controlsDate3],
      (val) => {
        checkControlsFields(val, 3);
      }
    );
    watch(
      [controlsType4, controlsNum4, controlsGrade4, controlsDate4],
      (val) => {
        checkControlsFields(val, 4);
      }
    );
    watch(
      [controlsType5, controlsNum5, controlsGrade5, controlsDate5],
      (val) => {
        checkControlsFields(val, 5);
      }
    );

    const addControlFields = () => {
      if (controls1.value == false) {
        controls1.value = true;

        switch (controlsShown.length) {
          case 0:
            controlsOrder1.value = 1;
            break;
          case 1:
            controlsOrder1.value = 2;
            break;
          case 2:
            controlsOrder1.value = 3;
            break;
          case 3:
            controlsOrder1.value = 4;
            break;
          case 4:
            controlsOrder1.value = 5;
            break;

          default:
            break;
        }

        controlsShown.push("controls1");
      } else if (controls1.value == true && controls2.value == false) {
        controls2.value = true;
        switch (controlsShown.length) {
          case 0:
            controlsOrder2.value = 1;
            break;
          case 1:
            controlsOrder2.value = 2;
            break;
          case 2:
            controlsOrder2.value = 3;
            break;
          case 3:
            controlsOrder2.value = 4;
            break;
          case 4:
            controlsOrder2.value = 5;
            break;

          default:
            break;
        }
        controlsShown.push("controls2");
      } else if (controls2.value == true && controls3.value == false) {
        controls3.value = true;
        switch (controlsShown.length) {
          case 0:
            controlsOrder3.value = 1;
            break;
          case 1:
            controlsOrder3.value = 2;
            break;
          case 2:
            controlsOrder3.value = 3;
            break;
          case 3:
            controlsOrder3.value = 4;
            break;
          case 4:
            controlsOrder3.value = 5;
            break;

          default:
            break;
        }
        controlsShown.push("controls3");
      } else if (controls3.value === true && controls4.value === false) {
        controls4.value = true;
        switch (controlsShown.length) {
          case 0:
            controlsOrder4.value = 1;
            break;
          case 1:
            controlsOrder4.value = 2;
            break;
          case 2:
            controlsOrder4.value = 3;
            break;
          case 3:
            controlsOrder4.value = 4;
            break;
          case 4:
            controlsOrder4.value = 5;
            break;

          default:
            break;
        }
        controlsShown.push("controls4");
      } else if (controls4.value === true && controls5.value === false) {
        controls5.value = true;
        switch (controlsShown.length) {
          case 0:
            controlsOrder5.value = 1;
            break;
          case 1:
            controlsOrder5.value = 2;
            break;
          case 2:
            controlsOrder5.value = 3;
            break;
          case 3:
            controlsOrder5.value = 4;
            break;
          case 4:
            controlsOrder5.value = 5;
            break;

          default:
            break;
        }
        controlsShown.push("controls5");
      }
